
<template>
    <div>
        <div class="loginNavFix">
            <img src="../../../assets/login/return.jpg" alt="" class="return" @click="returns">
            <p class="title f30 cfff text-center">德育详情</p>
        </div>
        <div class="mseeage">
            <img class="msg-head l" :src="data.stuHeadImage" alt="">
            <div class="msg-content l ml-36">
                <div class="overflow mt-10">
                    <div class="f26 c333 l">姓名：</div>
                    <div class="f26 cc3 l ml-10">{{data.studentName}}</div>
                </div>
                <div class="overflow mt-20">
                    <div class="f26 c333 l">班级：</div>
                    <div class="f26 cc3 l ml-10">{{data.classesName}}</div>
                </div>
                <div class="overflow mt-20">
                    <div class="f26 c333 l">学号：</div>
                    <div class="f26 cc3 l ml-10">{{data.stuNum}}</div>
                </div>
            </div>
        </div>
        <div class="content">
            <div class="content-texts">
                <div class="context-title">
                    <div class="rule l f30 c333">{{data.evalItemName}}</div>
                    <div class="math r f24 c333">{{data.score}}</div>
                </div>
                <div class="content-time f18 c999 mt-20">{{data.createTime | fileTime(data.createTime)}}</div>
                <div class="content-detail f26 c999 mt-36">
                    {{data.evalDesc}}
                </div>
            </div>
        </div>
        <div class="centerBtn f26 cfff text-center" @click="pushToParent">推送给家长</div>
    </div>
</template>

<script>
import {publisFn} from '../../../lib/util'
export default {
  name: 'HelloWorld',
    data () {
        return {
            data : {}
        }
    },
    methods : {
        returns(){
            this.$router.go(-1)//返回上一级
        },
        pushToParent(){
            this.$toast('向家长打小报告中。。。。');
        },
        getDetails(){
            this.$http({url:this.$api.moraleducation.moraleducationdetail},{evalStudentId:this.$route.query.id}).then(data => {
                console.log(data);
                if(data.code == 0){
                    data.data.stuHeadImage = this.$api.fileUrl + data.data.stuHeadImage;
                    this.data = data.data;
                }
            })
        }

    },
    filters : {
        fileTime(num){
           return publisFn.dateFormat(num);
        }
    },
    created() {
        var id = this.$route.query;
        this.getDetails();
    },
}
</script>

<style scoped>
    .mseeage{width: 100%;padding: 0.3rem;box-sizing: border-box;margin-top: 0.84rem;overflow: hidden;}
    .msg-head{width: 1.6rem;height: 1.6rem;border-radius: 50%;display: block;}
    .overflow{overflow: hidden;}
    .centerBtn{height: 0.9rem;width: 6.9rem;border-radius: 0.9rem; background: linear-gradient(to bottom right, #82cbff , #7b7ceb);line-height: 0.9rem; box-shadow: 0 0 10px #f0f9ff;position: fixed;bottom: 0.6rem;left: 50%;margin-left: -3.45rem;}
    .content{width: 100%;padding: 0.3rem;box-sizing: border-box;}
    .content-texts{background: #f3f3f3;padding: 0.35rem 0.3rem;}
    .context-title{overflow: hidden;}
    .content-detail{line-height: 0.45rem}
</style>





